<template>
  <div class="header">
    <nav class="nav-bar">
      <button class="fas fa-bars fa-2x toggle-haeder" ref="toggleNav"></button>
      <ul class="links" ref="links">
        <li>
          <router-link to="/" exact class="home-text">Home</router-link>
          <router-link to="/" exact class="home-icon">
            <i class="fas fa-home fa-2x"></i>
          </router-link>
        </li>
        <li>
          <router-link to="/about">Contact Us</router-link>
        </li>
        <li>
          <router-link to="/help">Products</router-link>
        </li>
        <li>
          <router-link to="/reda">Reda</router-link>
        </li>
      </ul>
    </nav>
    <div class="page-logo">
      <router-link to="/">
        <img alt="Vue logo" src='../../../assets/Logo.png' />
      </router-link>
    </div>
  </div>
</template>
<script>
export default {
  name: "Header-app",
  data() {
    return {
      isDragging: false, // لتتبع إذا كان المستخدم يقوم بالسحب
      startX: 0, // نقطة البداية للسحب
    };
  },
  mounted() {
    const button = this.$refs.toggleNav;
    const nav = this.$refs.links;

    // مستمع النقر على الزر
    button.addEventListener("click", (e) => {
      e.stopPropagation(); // منع الحدث من الانتقال إلى المستند
      nav.classList.toggle("show");
    });

    // مستمع النقر في أي مكان داخل المستند لإغلاق القائمة
    document.addEventListener("click", (e) => {
      if (!button.contains(e.target) && !nav.contains(e.target)) {
        nav.classList.remove("show");
      }
    });

    // استخدام الحدث المفوض لإغلاق القائمة عند النقر على أي رابط أو زر داخلها
    nav.addEventListener("click", (e) => {
      if (e.target.tagName === "A" || e.target.tagName === "BUTTON") {
        nav.classList.remove("show");
      }
    });

    // مستمعات السحب للأجهزة المحمولة
    if ("ontouchstart" in window) {
      document.addEventListener("touchstart", this.handleTouchStart);
      document.addEventListener("touchmove", this.handleTouchMove);
      document.addEventListener("touchend", this.handleTouchEnd);
    }
  },
  methods: {
    // بدء السحب
    handleTouchStart(e) {
      const nav = this.$refs.links;
      if (nav.classList.contains("show")) {
        this.isDragging = true;
        this.startX = e.touches[0].clientX; // حفظ النقطة التي بدأ فيها السحب
      }
    },

    // متابعة السحب
    handleTouchMove(e) {
      const nav = this.$refs.links;
      if (this.isDragging && nav.classList.contains("show")) {
        const moveDistance = e.touches[0].clientX - this.startX; // حساب المسافة التي تم سحبها
        if (moveDistance < -100) {
          // إذا تم السحب أكثر من 100 بكسل إلى اليسار
          nav.classList.remove("show");
          this.isDragging = false;
        }
      }
    },

    // إنهاء السحب
    handleTouchEnd() {
      this.isDragging = false;
    },
  },
  beforeDestroy() {
    const nav = this.$refs.links;

    // إزالة مستمعات النقر
    document.removeEventListener("click", this.handleDocumentClick);

    // إزالة مستمعات السحب للأجهزة المحمولة
    if ("ontouchstart" in window) {
      document.removeEventListener("touchstart", this.handleTouchStart);
      document.removeEventListener("touchmove", this.handleTouchMove);
      document.removeEventListener("touchend", this.handleTouchEnd);
    }

    // إزالة مستمع النقر على الروابط داخل القائمة
    nav.removeEventListener("click", this.handleNavClick);
  },
};
</script>

<!-- ربط الـ SCSS -->
<style scoped>
@import './header.css'; /* قم بتعديل المسار حسب موقع الملف */
</style>