<template>
  <div class="landing sections" id="home">
    <div
      class="overlay image"
      ref="mainImage"
      :class="{ 'fade-out': isFading, 'fade-in': !isFading }"
      :style="{ backgroundImage: `url(${images[currentIndex]})` }"
    >
      <div v-if="!images.length">
        <p>No images found</p>
      </div>
    </div>

    <div class="png-image">
      <img src="../../assets/landing/s110.png" alt="" />
      <img src="../../assets/landing/s100.png" alt="" />
    </div>
    <div class="text">
      <h1 class="selection" id="home">
        Hello Every one!
        <br />
        i'm
        <span class="selection">Reda</span>
        i make art.
      </h1>
      <button class="page-button">Get Your Page</button>
    </div>
    <div class="phone">
      <span>01070010523</span>
    </div>
  </div>
</template>
<script>
export default {
  name: "page-landing",
  data() {
    return {
      images: [], // لتخزين مسارات الصور
      currentIndex: 0, // الصورة الحالية
      isFading: false, // لإدارة تأثير الشفافية
    };
  },
  created() {
    // استخدام require.context لاستيراد جميع الصور من مجلد معين
    const imagesContext = require.context(
      "@/assets/landing/main",
      false,
      /\.(jpg|png)$/
    );
    this.images = imagesContext.keys().map((image) => imagesContext(image));

    if (this.images.length === 0) {
      console.warn("No images found in the specified directory.");
      return;
    }

    // استرجاع الـ currentIndex من localStorage إذا كان موجودًا
    const savedIndex = localStorage.getItem("currentImageIndex");
    if (savedIndex !== null) {
      this.currentIndex = parseInt(savedIndex, 10);
    }
  },
  mounted() {
    if (this.images.length > 0) {
      this.startImageRotation();
    }
  },
  methods: {
    startImageRotation() {
      const mainImage = this.$refs.mainImage;

      setInterval(() => {
        if (mainImage) {
          // إضافة تأثير الشفافية
          this.isFading = true;

          // انتظار انتهاء تأثير الشفافية قبل تغيير الصورة
          setTimeout(() => {
            this.currentIndex = (this.currentIndex + 1) % this.images.length;
            localStorage.setItem("currentImageIndex", this.currentIndex);

            // إزالة تأثير الشفافية بعد التغيير
            this.isFading = false;
          }, 1000); // مدة تأثير fade-out (1 ثانية)
        }
      }, 5000); // مدة التبديل (5 ثواني)
    },
  },
};
</script>

<style scoped>
@import "./landing.css";
</style>
